/* CSS da Lista de Usu√°rios para Pagamento */

/* COLOR SCHEME

    #c8d8e4

*/

@font-face {
    font-family: 'Roboto-Regular';
    src: local('Roboto-Regular'), url(./fonts/Roboto-Regular.ttf) format('truetype');
}

body {
    margin: 0;
    padding: 0;
    background-color: #f2f2f2;
}

.teste {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: stretch;
    justify-content: space-around;
    transition: 0.3s;
}

.teste#blur.aberto {
    filter: blur(5px) grayscale(50%);
    pointer-events: none;
    user-select: none;
}

.container {
    width: 50%;
    max-width: 140px;
    height: 200px;
    background-color: #ffffff;
    padding: 1.25rem;
    margin: 12px;
    border: 1px solid;
    border-radius: 15px 0px;
    border-color: #ffffff;
    box-shadow: 5px 5px 10px #888888;
}

.thumbnail {
    width: 100%;
    height: 100%;
    display: block;
    margin: auto;
    border-radius: 15px 0px;
}

.profile-container {
    position: relative;
}

.btn-pgto {
    position: absolute;
    right: -10px;
    bottom: -15px;
    width: 30%;
    height: 30%;
    background-color: #52ab98;
    background-repeat: no-repeat;
    border: none;
    border-radius: 100%;
    text-align: center;
    line-height: 50%;
    cursor: pointer;
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
    transition: 0.2s all;
}

.btn-pgto:active {
    transform: scale(0.98);
    /* Scaling button to 0.98 to its original size */
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
    /* Lowering the shadow */
}

.icon {
    filter: invert(100%) sepia(9%) saturate(7485%) hue-rotate(213deg) brightness(124%) contrast(107%);
}

.infos {
    margin-right: auto;
}

.infos p {
    margin: 0;
    font-family: Roboto-Regular;
}

.item-id {
    padding-top: 4px;
    padding-bottom: 2px;
    font-size: 10px;
    font-style: italic;
    color: #696969;
}

.item-name {
    color: #000000;
}

.item-username {
    font-size: 12px;
    color: #696969;
}

.btn-pgto2 {
    visibility: hidden;
}

