/* CSS dos Modals */

.abrirModal {
    display: flex;
    position: fixed;
    top: 50%;
    left: 50%;
    right: 'auto';
    bottom: 'auto';
    margin-right: -50%;
    transform: translate(-50%, -50%);
    width: 310px;
    flex-direction: column;
    background-color: white;
    justify-content: space-between;
    align-items: center;
    transition: 0.3s;
    border: 1px solid;
    border-radius: 15px 0px;
    border-color: #ffffff;
    box-shadow: 5px 5px 10px #888888;
}

.modal-body {
    position: relative;
    width: 100%;
    height: 100%;
}

.texto-cabecalho-modal {
    font-family: Roboto-Regular;
    color: #2b6777;
    padding: 10px;
    width: 94%;
    margin-top: 0;
    align-content: center;
    font-weight: bold;
}

.abrirModal select {
    width: 110px;
    height: 30px;
    border-radius: 5px;
    border-color: #f2f2f2;
}

.abrirModal p {
    text-align: center;
    font-family: Roboto-Regular;
}

input {
    border-radius: 5px;
    width: 100px;
    height: 30px;
    border-color: #f2f2f2;
}

.valorInput {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
}

#alert-lbl {
    margin: 0;
    color: #E78587;
    font-size: 10px;
    font-weight: bold;
    font-family: Roboto-Regular;
    margin-top: 2%;
}

.btn-pgto-final {
    margin: 20px;
    width: 50px;
    height: 50px;
    background-color: #52ab98;
    background-repeat: no-repeat;
    border: none;
    border-radius: 100%;
    text-align: center;
    line-height: 50%;
    cursor: pointer;
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
    transition: 0.2s all;
}

.btn-pgto-final:active {
    transform: scale(0.98);
    /* Scaling button to 0.98 to its original size */
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
    /* Lowering the shadow */
}

#modal-close {
    position: absolute;
    top: -5%;
    right: -5%;
    width: 35px;
    height: 35px;
    background-color: #E78587;
    background-repeat: no-repeat;
    border: none;
    border-radius: 100%;
    text-align: center;
    line-height: 50%;
    cursor: pointer;
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
    transition: 0.2s all;
}


/* Media Queries */

@media screen and (max-width: 412px) {
    .teste {
        display: block;
    }

    .container {
        display: flex;
        width: 100%;
        max-width: 100%;
        height: 160px;
        background-color: #ffffff;
        margin: auto;
        border: none;
        border-radius: 0;
        border-color: #ffffff;
        box-shadow: 5px 5px 10px #888888;
        flex-direction: row;
        align-content: flex-start;
        box-sizing: border-box;
    }

    .profile-container {
        height: 100%;
    }

    .thumbnail {
        width: 120px;
        height: 100%;
        display: block;
        margin: auto;
        border-radius: 15px 0px;
    }

    .btn-pgto {
        visibility: hidden;
    }

    .infos {
        margin-left: 12px;
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        justify-content: center;

    }

    .btn-container {
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        justify-content: center;
    }

    .btn-pgto2 {
        width: 50px;
        height: 50px;
        background-color: #52ab98;
        background-repeat: no-repeat;
        border: none;
        border-radius: 100%;
        text-align: center;
        line-height: 50%;
        cursor: pointer;
        box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
        transition: 0.2s all;
        visibility: visible;
    }
    
    .btn-pgto:active2 {
        transform: scale(0.98);
        /* Scaling button to 0.98 to its original size */
        box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
        /* Lowering the shadow */
    }
}

